import _ from 'lodash';
import { LibraryDelta } from '@additor/delta';
import { Folder, TypeNumbers } from '@additor/elements';
import { Utils } from '@additor/adele-client';
import generateResponse from 'common/generateResponse';
import AdeleClient from 'background/core/AdeleClient';
import { getCardId } from 'background/stroage/card';
import { setRecentFolderId } from 'background/stroage/folder';
import { errorHandling } from '../index';

export default async function(message, sender, sendResponse) {
  errorHandling(async () => {
    const {
      data: { name, moveCard = false },
    } = message;

    const updateDelta = new LibraryDelta();
    const localId = Utils.generateLocalElementId();
    const folder = new Folder({
      id: localId,
      name,
    });
    const newLayout = {
      type: -1,
      _type: folder.type,
      id: localId,
      parent: null,
    };

    updateDelta.create(folder, { a: 'create' }).create(newLayout);
    const mutateDeltaShell = await AdeleClient.mutateDelta(updateDelta);
    const createdFolder = _.get(
      _.values(mutateDeltaShell.d.created.folders),
      '0.0',
    );

    if (!moveCard) {
      sendResponse(
        generateResponse({
          data: { createdFolder, moveCard },
        }),
      );
      return;
    }

    const cardId = getCardId(sender.tab.id);
    const updateCardLayoutDelta = new LibraryDelta();
    const originCardLayout = { type: -1, _type: TypeNumbers.CARD, id: cardId };
    const nextCardLayout = {
      ...originCardLayout,
      parent: createdFolder.id,
      prev: null,
    };
    updateCardLayoutDelta.update(originCardLayout, nextCardLayout, {
      a: 'move',
    });
    await AdeleClient.mutateDelta(updateCardLayoutDelta);
    setRecentFolderId(createdFolder.id || '');

    sendResponse(
      generateResponse({
        data: { createdFolder, moveCard },
      }),
    );
  }, sendResponse);
}
