import * as Elements from '@additor/elements';
import { WorkspaceDelta, Registration } from '@additor/delta';

WorkspaceDelta.register(
  new Registration(
    'workspace',
    Elements.TypeNumbers.WORKSPACE,
    Elements.Workspace,
    true,
  ),
);
WorkspaceDelta.register(
  new Registration('channels', Elements.TypeNumbers.CHANNEL, Elements.Channel),
);
WorkspaceDelta.register(
  new Registration('pages', Elements.TypeNumbers.PAGE, Elements.Page),
);
WorkspaceDelta.register(new Registration('layouts', -1));
WorkspaceDelta.register(
  new Registration(
    'relations',
    Elements.TypeNumbers.RELATION,
    Elements.Relation,
  ),
);

export default WorkspaceDelta;
