import generateResponse from 'common/generateResponse';
import { loadHighlightable } from 'background/stroage/settings';
import { errorHandling } from './index';

export default async function(message, sender, sendResponse) {
  errorHandling(async () => {
    const highlightable = await loadHighlightable();
    sendResponse(
      generateResponse({
        data: {
          settings: {
            highlightable,
          },
        },
      }),
    );
  }, sendResponse);
}
