import generateResponse from 'common/generateResponse';
import { getGuideStatus } from 'background/stroage/guide';
import { errorHandling } from '../index';

export default async function(message, sender, sendResponse) {
  errorHandling(async function handleCheckGuideVisibility() {
    const {
      // available guidName: [LIBRARY_GUIDE || CREATE_FOLDER_GUIDE || OPEN_LIBRARY_GUIDE]
      //  === ['library_guide', 'create_folder_guide' || 'open_library_guide'];
      data: { guideName },
    } = message;

    const isGuideShowed = await getGuideStatus(guideName);
    let visibility = !isGuideShowed;

    sendResponse(
      generateResponse({
        data: { visibility },
      }),
    );
  }, sendResponse);
}
