import { fetchRequest } from './fetchRequest';

export async function fetchGet(url, options, useToken = true) {
  return await fetchRequest({
    url,
    options: { ...options, method: 'GET' },
    useToken,
  });
}

export async function fetchPost(url, options, useToken = true) {
  return await fetchRequest({
    url,
    options: { ...options, method: 'POST' },
    useToken,
  });
}

export async function fetchPut(url, options, useToken = true) {
  return await fetchRequest({
    url,
    options: { ...options, method: 'PUT' },
    useToken,
  });
}

export async function fetchDelete(url, options, useToken = true) {
  return await fetchRequest({
    url,
    options: {
      ...options,
      method: 'DELETE',
    },
    useToken,
  });
}
