import axios from 'axios';
import { getAccessToken } from '../../token/accessToken';

import { getAppAPIHost, getWorkspaceAPIHost } from '../APIHost';

export function composeResponse(response = {}) {
  const {
    status = null,
    statusText = null,
    data: body = null,
    config = null,
  } = response;
  return {
    status,
    statusText,
    config,
    body,
  };
}

export async function fetchRequest({ url, options, host, version, useToken }) {
  const { method, params = {}, body = {}, headers = {} } = options;

  if (useToken) {
    headers['X-Additor-Access'] = await getAccessToken();
  }

  try {
    const res = await axios({
      url,
      method,
      baseURL: `${
        host === 'app' ? getAppAPIHost() : getWorkspaceAPIHost()
      }/api/${version || 'v2'}`,
      headers: {
        'Content-Type': 'application/json',
        'access-control-allow-credentials': true,
        ...headers,
      },
      params,
      data: body,
      withCredentials: true,
    });
    return {
      ok: true,
      ...composeResponse(res),
    };
  } catch (error) {
    if (error.response) {
      return {
        ok: false,
        ...composeResponse(error.response),
        error,
      };
    }
    throw error;
  }
}
