import CommandTypes from 'common/CommandTypes';
import {
  saveHighlightable,
  loadHighlightable,
} from 'background/stroage/settings';
import { getCurrentTab } from '../chrome/tab';
import sendMessageToShowDisableHighlightAlert from '../sendMessage/sendMessageToShowDisableHighlightAlert';
import sendMessageToShowEnableHighlightAlert from '../sendMessage/sendMessageToShowEnableHighlightAlert';
import onClickBrowserActionIcon from './onClickBrowserActionIcon';
import _ from 'lodash';

export default function onCommand(command) {
  const actions = {
    [CommandTypes.CREATE_CARD]() {
      getCurrentTab(tab => {
        onClickBrowserActionIcon(tab);
      });
    },
    async [CommandTypes.SWITCH_HIGHLIGHT_MODE]() {
      const highlightable = await loadHighlightable();
      const nextHighlightable = !highlightable;
      await saveHighlightable(nextHighlightable);
      getCurrentTab(tab => {
        if (nextHighlightable) {
          sendMessageToShowEnableHighlightAlert(tab);
        } else {
          sendMessageToShowDisableHighlightAlert(tab);
        }
      });
    },
  };

  const actionFunc = actions[command];
  if (_.isFunction(actionFunc)) {
    try {
      actionFunc();
    } catch (e) {
      // error
    }
  }
}
