import { getWorkspaceAPIHost } from '../api/APIHost';
import {
  getCookie,
  COOKIE_NAMES,
  removeExchangeTokenFromCookie,
} from '../utils/cookieManager';
import postAccess from '../api/rest/access/postAccess';

// const ACCESS_TOKEN_EXPIRATION_TIME = 1000 * 60 * 60 * 24; // 24 hour
const ACCESS_TOKEN_EXPIRATION_TIME = 1000 * 5;
let _accessToken = '';
let _accessTokenExpiredDate = 0;

async function fetchAccessToken() {
  const exchangeToken = await getCookie(COOKIE_NAMES.EXCHANGE);
  if (exchangeToken) {
    let res = null;
    try {
      res = await postAccess(exchangeToken);
    } catch (e) {
      console.error(e);
      return null;
    }
    const {
      body: { accessToken: accessTokenInResponse },
    } = res;

    if (!accessTokenInResponse) {
      removeExchangeTokenFromCookie();
      return null;
    }
    return accessTokenInResponse;
  }
  return null;
}

export async function getAccessToken(forceReissue = false) {
  if (Date.now() <= _accessTokenExpiredDate && !forceReissue) {
    return _accessToken;
  }
  const workspaceAPIHost = getWorkspaceAPIHost();
  if (!workspaceAPIHost) {
    return '';
  }
  let latestAccessToken = await fetchAccessToken();
  if (!latestAccessToken) {
    return '';
  }

  _accessToken = latestAccessToken;
  _accessTokenExpiredDate = Date.now() + ACCESS_TOKEN_EXPIRATION_TIME;
  return _accessToken;
}

