import {
  load as loadFromStorage,
  save as saveToStorage,
  STORAGE_KEYS,
} from '../chrome/storage';

export async function saveImmediatelyOpenWidget(tab) {
  return saveToStorage(STORAGE_KEYS.IMMEDIATELY_OPEN_WIDGET_IN_TAB, tab);
}

export async function loadImmediatelyOpenWidget() {
  return loadFromStorage(STORAGE_KEYS.IMMEDIATELY_OPEN_WIDGET_IN_TAB);
}
