import { LibraryDelta } from '@additor/delta';
import { TypeNumbers } from '@additor/elements';
import generateResponse from 'common/generateResponse';
import AdeleClient from 'background/core/AdeleClient';
import { getCardId } from 'background/stroage/card';
import { setRecentFolderId } from 'background/stroage/folder';
import { errorHandling } from '../index';

export default async function(message, sender, sendResponse) {
  errorHandling(async () => {
    const {
      data: { id, parent = null },
    } = message;

    const cardId = getCardId(sender.tab.id);

    const updateCardLayoutDelta = new LibraryDelta();
    const originCardLayout = { type: -1, _type: TypeNumbers.CARD, id: cardId };
    const nextCardLayout = {
      ...originCardLayout,
      parent,
      prev: null,
    };
    updateCardLayoutDelta.update(originCardLayout, nextCardLayout, {
      a: 'move',
    });
    await AdeleClient.mutateDelta(updateCardLayoutDelta);
    setRecentFolderId(parent || '');

    sendResponse(
      generateResponse({
        data: { id, parent },
      }),
    );
  }, sendResponse);
}
