import { Frontend, EventChannels } from '@additor/adele-client';
import LibraryDelta from 'background/core/delta/LibraryDelta';
import WorkspaceDelta from 'background/core/delta/WorkspaceDelta';
import { getAccessToken } from '../../token/accessToken';

const frontend = new Frontend({
  deltas: {
    [EventChannels.WORKSPACE_DELTA]: {
      deltaClass: WorkspaceDelta,
    },
    [EventChannels.LIBRARY_DELTA]: {
      deltaClass: LibraryDelta,
    },
  },
  retrieveToken: async () => {
    return await getAccessToken();
  },
});

export default frontend;
