import _ from 'lodash';
import Frontend from './Frontend';
import HttpRequestPlugin from './HttpRequestPlugin';
import WebSocketPlugin from './WebSocketPlugin';
import { TuneEvents } from '@additor/adele-client';
import { TypeNumbers } from '@additor/elements';
import WorkspaceDelta from '../delta/WorkspaceDelta';
import LibraryDelta from '../delta/LibraryDelta';
import delay from '../../utils/delay';
import { handleError } from '../utils';
// import * as Events from './events';
import {
  setWaitingRequests,
  getWaitingRequests,
  pushWaitingRequest,
} from './waitingRequests';

class AdeleClient {
  constructor() {
    this.workspaceTune = null;
    this.libraryTune = null;
    this.waitingRequests = [];
  }

  listenWorkspaceTune({
    onMutatingSuccess = _.noop,
    onQueryingSuccess = _.noop,
    onMutatingFail = _.noop,
    onQueryingFail = _.noop,
    onDestroyed = _.noop,
  }) {
    if (!this.workspaceTune) return;
    if (this.workspaceTune.baseVersion) {
      // baseVersion     Tune  delta  .
      onQueryingSuccess(this.workspaceTune.delta);
    } else {
      // baseVersion      .
      this.workspaceTune.on(TuneEvents.QUERYING_SUCCESS, deltaShell => {
        onQueryingSuccess(deltaShell.d);
      });
    }
    this.workspaceTune.on(TuneEvents.MUTATING_SUCCESS, deltaShell => {
      const resolved = _.some(this.waitingRequests, request => {
        if (request.checkOwnRequest(deltaShell.d)) {
          request.resolve(deltaShell.d);
          return true;
        }
        return false;
      });
      if (!resolved) {
        onMutatingSuccess(deltaShell.d);
      }
    });
    this.workspaceTune.on(TuneEvents.MUTATING_FAIL, () => {
      onMutatingFail();
    });
    this.workspaceTune.on(TuneEvents.QUERYING_FAIL, () => {
      onQueryingFail();
    });
    this.workspaceTune.on(TuneEvents.DESTROYED, () => {
      onDestroyed();
    });
  }

  async setWorkspaceTune({
    workspaceId,
    userId,
    clientAgentId,
    accessToken,
    onMutatingSuccess,
    onQueryingSuccess,
    onMutatingFail,
    onQueryingFail,
    onDestroyed,
  }) {
    if (this.workspaceTune) {
      try {
        this.workspaceTune.removeAllListeners();
        this.unsubscribeLibraryTune();
        await Frontend.checkout();
      } catch (e) {
        // console.warn('');
      } finally {
        this.workspaceTune = null;
        Frontend.deactivate();
      }
    }

    Frontend.registerHttpRequestPlugin(new HttpRequestPlugin())
      .registerWebSocketPlugin(new WebSocketPlugin())
      .registerIdentification(workspaceId, userId, clientAgentId, accessToken)
      .activate(e => {
        console.error('delta error', e);
      });
    await delay(1000);
    this.workspaceTune = await Frontend.checkin();
    this.listenWorkspaceTune({
      onMutatingSuccess,
      onQueryingSuccess,
      onMutatingFail,
      onQueryingFail,
      onDestroyed,
    });
    await delay(500);

    return true;
  }

  getWorkspaceDelta() {
    return this.workspaceTune.delta;
  }

  mutateDelta(delta) {
    if (delta instanceof WorkspaceDelta) {
      return this.workspaceTune.mutate(delta);
    } else if (delta instanceof LibraryDelta && this.libraryTune) {
      return this.libraryTune.mutate(delta);
    }
    return null;
  }

  generatePromise(updateDelta, checkOwnRequest) {
    return new Promise(resolve => {
      const request = {
        checkOwnRequest,
        resolve: delta => {
          setWaitingRequests(
            _.filter(
              getWaitingRequests(),
              waitingRequest => waitingRequest !== request,
            ),
          );
          resolve(delta);
        },
      };
      pushWaitingRequest(request);
      this.mutateDelta(updateDelta);
    });
  }

  getLibraryTune() {
    return this.libraryTune;
  }

  listenLibraryTune({
    onQueryingSuccess = _.noop,
    onMutatingSuccess = _.noop,
    onMutatingFail = _.noop,
    onSyncSuccess = _.noop,
    onInjectSuccess = _.noop,
  }) {
    if (!this.libraryTune) return;
    this.libraryTune.on(TuneEvents.QUERYING_SUCCESS, onQueryingSuccess);
    this.libraryTune.on(TuneEvents.MUTATING_SUCCESS, onMutatingSuccess);
    this.libraryTune.on(TuneEvents.MUTATING_FAIL, onMutatingFail);
    this.libraryTune.on(TuneEvents.SYNC_SUCCESS, onSyncSuccess);
    this.libraryTune.on(TuneEvents.INJECTING_SUCCESS, onInjectSuccess);
  }

  async subscribeLibraryTune({
    onSubscribingSuccess = _.noop,
    onQueryingSuccess = _.noop,
    onMutatingSuccess = _.noop,
    onMutatingFail = _.noop,
    onSyncSuccess = _.noop,
    onInjectSuccess = _.noop,
  }) {
    if (!this.workspaceTune) return;
    try {
      if (this.libraryTune) {
        Frontend.unsubscribeLibrary();
      }
      this.libraryTune = await Frontend.subscribeLibrary();
      onSubscribingSuccess(this.libraryTune.delta);
      this.listenLibraryTune({
        onQueryingSuccess,
        onMutatingSuccess,
        onMutatingFail,
        onSyncSuccess,
        onInjectSuccess,
      });
    } catch (error) {
      handleError(error);
    }
  }

  unsubscribeLibraryTune() {
    try {
      if (!this.libraryTune) return;
      Frontend.unsubscribeLibrary();
      this.libraryTune = null;
    } catch (error) {
      handleError(error);
    }
  }

  getLibraryDelta() {
    return this.libraryTune?.delta || null;
  }

  deleteLibraryCard(id) {
    const updateDelta = new LibraryDelta().delete(
      {
        type: TypeNumbers.CARD,
        id,
      },
      { a: 'archive' },
    );
    return this.mutateDelta(updateDelta);
  }
}

export default new AdeleClient();
