import _ from 'lodash';
import { LibraryDelta } from '@additor/delta';
import { Card } from '@additor/elements';
import { Utils } from '@additor/adele-client';
import generateResponse from 'common/generateResponse';
import { setCardId } from 'background/stroage/card';
import AdeleClient from 'background/core/AdeleClient';
import { errorHandling } from '../index';

export default async function(message, sender, sendResponse, callbacks = {}) {
  errorHandling(async () => {
    const tabId = sender.tab.id;
    const {
      data: { cardUri, parent = null },
    } = message;

    const updateDelta = new LibraryDelta();
    const localId = Utils.generateLocalElementId();
    const card = new Card({
      id: localId,
      ref: {
        eid: cardUri,
      },
    });
    const newLayout = {
      type: -1,
      _type: card.type,
      id: localId,
      parent,
    };
    updateDelta.create(card, { a: 'create' }).create(newLayout);
    const mutateDeltaShell = await AdeleClient.mutateDelta(updateDelta);
    const createdCard = _.get(
      _.values(mutateDeltaShell.d.created.cards),
      '0.0',
    );

    setCardId({ tabId, cardId: createdCard.id });
    sendResponse(
      generateResponse({
        data: { createdCard },
      }),
    );
    if (_.isFunction(callbacks.onCreateCard)) {
      callbacks.onCreateCard(createdCard);
    }
  }, sendResponse);
}
