import SocketIO from 'socket.io-client';

import * as EventHandler from './EventHandler';
import * as EventEmitter from './EventEmitter';
import SocketEvents from './SocketEvents';

let socketIO;

export default function connectWebSocket(webSocketHost) {
  const webSocketURL = `${webSocketHost}/v2/connection`;
  socketIO = new SocketIO(webSocketURL, {
    transports: ['websocket'],
  });

  socketIO.on(SocketEvents.CONNECT, EventHandler.handleConnect);
  socketIO.on(SocketEvents.CONNECT_ERROR, EventHandler.handleConnectError);
  socketIO.on(SocketEvents.DISCONNECT, EventHandler.handleDisconnect);
  socketIO.on(SocketEvents.CONNECT_TIMEOUT, EventHandler.handleConnectTimeout);
  socketIO.on(SocketEvents.TIMING, EventHandler.handleTiming);
  socketIO.on(SocketEvents.ENTER_PAGE, EventHandler.handleEnterPage);
  socketIO.on(SocketEvents.LEAVE_PAGE, EventHandler.handleLeavePage);
  socketIO.on(SocketEvents.FOCUS_PAGE, EventHandler.handleFocusPage);
  socketIO.on(SocketEvents.CHECK_NOTE, EventHandler.handleCheckNote);
  socketIO.on(SocketEvents.PING, EventHandler.handlePing);
  socketIO.on(SocketEvents.PONG, EventHandler.handlePong);
  socketIO.on(SocketEvents.Web.NOTIFY, EventHandler.handleNotify);
  socketIO.on(SocketEvents.Web.SYNCHRONIZE, EventHandler.handleSync);
  socketIO.on(SocketEvents.Web.CURSOR, EventHandler.handleCursorChange);

  EventEmitter.setWebSocketInstance(socketIO);
  return socketIO;
}
