import _ from 'lodash';

export const STORAGE_KEYS = {
  CURRENT_WORKSPACE_ID: 'currentWorkspaceId',
  HIGHLIGHTABLE: 'highlightable',
  IMMEDIATELY_OPEN_WIDGET_IN_TAB: 'immediatelyOpenWidgetInTab',
  RECENT_SELECTED_FOLDER: 'recentSelectedFolder',
  GUIDE: 'guide',
};

export async function saveMap(key, value) {
  return new Promise((resolve, reject) => {
    if (_.isEmpty(value)) {
      reject();
    } else {
      chrome.storage.local.get([key], items => {
        const newData = {
          ...items[key],
          ...value,
        };
        // save(key, newData);

        chrome.storage.local.set(
          {
            [key]: newData,
          },
          () => {
            if (chrome.runtime.lastError) {
              reject();
            } else {
              resolve(true);
            }
          },
        );
      });
    }
  });
}

export function save(key, value) {
  const data = {
    [key]: value,
  };
  return new Promise((resolve, reject) => {
    if (_.isEmpty(data)) {
      reject();
    } else {
      chrome.storage.local.set(data, () => {
        if (chrome.runtime.lastError) {
          reject();
        } else {
          resolve(true);
        }
      });
    }
  });
}

export function load(key) {
  return new Promise((resolve, reject) => {
    chrome.storage.local.get([key], items => {
      if (chrome.runtime.lastError) {
        reject();
      } else if (_.isString(key)) {
        resolve(items && items[key]);
      } else {
        resolve(items);
      }
    });
  });
}

export function remove(key) {
  return new Promise((resolve, reject) => {
    chrome.storage.local.remove(key, items => {
      if (chrome.runtime.lastError) {
        reject();
      } else {
        resolve(items);
      }
    });
  });
}

export function clear() {
  return new Promise((resolve, reject) => {
    chrome.storage.local.clear(() => {
      if (chrome.runtime.lastError) {
        reject(false);
      } else {
        resolve(true);
      }
    });
  });
}
