import { fetchPost } from 'background/api/rest/v3';
import { TypeNumbers } from '@additor/elements';

export default async function postHighlights({ targetId, highlight }) {
  return await fetchPost('/highlights', {
    body: {
      ...highlight,
      target: {
        type: TypeNumbers.CARD,
        id: targetId,
      },
    },
  });
}
