import _ from 'lodash';
import generateResponse from 'common/generateResponse';
import { getCardId } from 'background/stroage/card';
import {
  deleteComment as deleteLocalComment,
  getComment,
  setComment,
} from 'background/stroage/comment';
import postComments from 'background/api/rest/comment/postComments';
import putComments from 'background/api/rest/comment/putComments';
import deleteComments from 'background/api/rest/comment/deleteComments';
import { errorHandling } from '../index';

export default async function(message, sender, sendResponse) {
  errorHandling(async () => {
    const {
      data: { commentMessage },
    } = message;
    const tabId = sender.tab.id;
    const localComment = getComment(tabId);
    if (_.isEmpty(commentMessage)) {
      if (localComment) {
        // delete comment
        await deleteComments(localComment.id);
        deleteLocalComment(tabId);
      } // if localComment not exist -> do nothing
      sendResponse(generateResponse());
    } else {
      if (localComment) {
        // update comment
        const result = await putComments({
          id: localComment.id,
          message: commentMessage,
        });

        if (!result.ok) {
          sendResponse(
            generateResponse({
              ok: false,
              error: result.error,
            }),
          );
        } else {
          const updatedComment = _.get(result, 'body.results[0]');
          setComment({
            ...localComment,
            message: commentMessage,
          });
          sendResponse(
            generateResponse({
              data: { updatedComment },
            }),
          );
        }
      } else {
        // create comment
        const result = await postComments({
          targetId: getCardId(tabId),
          message: commentMessage,
        });

        if (!result.ok) {
          sendResponse(
            generateResponse({
              ok: false,
              error: result.error,
            }),
          );
        } else {
          const createdComment = _.get(result, 'body.results[0]');
          setComment({ tabId, comment: createdComment });
          sendResponse(
            generateResponse({
              data: { createdComment },
            }),
          );
        }
      }
    }
  }, sendResponse);
}
