import _ from 'lodash';
import getHostName from 'common/getHostName';
import {
  fetchAndSetWorkspaces,
  getWorkspaces,
  getCurrentWorkspaceId,
  saveCurrentWorkspaceId,
  setWorkspaces,
  setCurrentWorkspaceId,
} from 'background/stroage/workspace';

import sendMessageToUpdateWorkspaces from 'background/sendMessage/sendMessageToUpdateWorkspaces';
import { COOKIE_NAMES } from 'background/utils/cookieManager';

const CAUSES = {
  EXPLICIT: 'explicit',
  EXPIRED: 'expired',
  OVERWRITE: 'overwrite',
};

export default async function onCookieChanged({ cause, cookie, removed }) {
  if (
    _.includes(cookie.domain, getHostName()) &&
    cookie.name === COOKIE_NAMES.WORKSPACE_EIDS &&
    cause === CAUSES.EXPLICIT
  ) {
    const workspaces = getWorkspaces();
    const currentWorkspaceId = getCurrentWorkspaceId();
    const currentWorkspace = _.find(
      workspaces,
      ({ id }) => id === currentWorkspaceId,
    );

    let nextWorkspaceEids = [];
    if (!removed) {
      try {
        nextWorkspaceEids = JSON.parse(decodeURIComponent(cookie.value));
      } catch (e) {
        nextWorkspaceEids = [];
      }
    }

    if (nextWorkspaceEids.length === 0) {
      await saveCurrentWorkspaceId('');
      setCurrentWorkspaceId('');
      setWorkspaces([]);
      return;
    }

    const nextWorkspaces = await fetchAndSetWorkspaces(nextWorkspaceEids);
    sendMessageToUpdateWorkspaces(nextWorkspaces);
    if (
      !currentWorkspace ||
      !_.includes(nextWorkspaceEids, currentWorkspace.eid)
    ) {
      if (_.isEmpty(nextWorkspaces)) {
        await saveCurrentWorkspaceId('');
        setCurrentWorkspaceId('');
      } else {
        const nextCurrentWorkspace = nextWorkspaces[0];
        await saveCurrentWorkspaceId(nextCurrentWorkspace.id);
        setCurrentWorkspaceId(nextCurrentWorkspace.id);
      }
    } else {
      setWorkspaces(nextWorkspaces);
    }
  }
}
