import * as Sentry from '@sentry/browser';
import _ from 'lodash';
import MessageTypes from 'common/MessageTypes';
import generateResponse from 'common/generateResponse';
import { saveImmediatelyOpenWidget } from 'background/stroage/temp';

import handleCheckGuideVisibility from './guide/handleCheckGuideVisibility';
import handleSetGuideShowed from './guide/handleSetGuideShowed';

import handleDeleteWorkspace from './workspace/handleDeleteWorkspace';
import handleSwitchWorkspace from './workspace/handleSwitchWorkspace';

import handleCreateCard from './card/handleCreateCard';
import handleDeleteCard from './card/handleDeleteCard';
import handleMoveCard from './card/handleMoveCard';
import handleUpdateCard from './card/handleUpdateCard';

import handleCreateFolder from './folder/handleCreateFolder';

import handleChangeCommentMessage from './comment/handleChangeCommentMessage';

import handleCreateHighlight from './highlight/handleCreateHighlight';
import handleDeleteHighlight from './highlight/handleDeleteHighlight';
import handleUpdateHighlightColor from './highlight/handleUpdateHighlightColor';

import handleEnableHighlight from './settings/handleEnableHighlight';
import handleDisableHighlight from './settings/handleDisableHighlight';

import handleGetInitialStates from './handleGetInitialStates';
import handleGetSettings from './handleGetSettings';
import handleResetWidgetOpenedPageId from './handleResetWidgetOpenedPageId';
import handleSetWidgetOpenedPageId from './handleSetWidgetOpenedPageId';

import { openSignInTab } from '../../utils/openSignInTab';

let onCreateCard = null;

function setOnCreateCard(func) {
  onCreateCard = func;
}

export default function onMessage(message, sender, sendResponse) {
  const asyncActions = {
    [MessageTypes.GET_INITIAL_STATES]() {
      handleGetInitialStates(message, sender, sendResponse);
    },
    [MessageTypes.CREATE_CARD]() {
      handleCreateCard(message, sender, sendResponse, {
        onCreateCard: createdCard => {
          if (_.isFunction(onCreateCard)) {
            onCreateCard(createdCard);
            onCreateCard = null;
          }
        },
      });
    },
    [MessageTypes.UPDATE_CARD]() {
      handleUpdateCard(message, sender, sendResponse);
    },
    [MessageTypes.DELETE_CARD]() {
      handleDeleteCard(message, sender, sendResponse);
    },
    [MessageTypes.MOVE_CARD]() {
      handleMoveCard(message, sender, sendResponse);
    },
    [MessageTypes.CREATE_FOLDER]() {
      handleCreateFolder(message, sender, sendResponse);
    },
    [MessageTypes.SWITCH_WORKSPACE]() {
      handleSwitchWorkspace(message, sender, sendResponse);
    },
    [MessageTypes.CHANGE_COMMENT_MESSAGE]() {
      handleChangeCommentMessage(message, sender, sendResponse);
    },
    [MessageTypes.CREATE_HIGHLIGHT]() {
      handleCreateHighlight(message, sender, sendResponse, {
        setOnCreateCard,
      });
    },
    [MessageTypes.UPDATE_HIGHLIGHT_COLOR]() {
      handleUpdateHighlightColor(message, sender, sendResponse);
    },
    [MessageTypes.DELETE_HIGHLIGHT]() {
      handleDeleteHighlight(message, sender, sendResponse);
    },
    [MessageTypes.GET_SETTINGS]() {
      handleGetSettings(message, sender, sendResponse);
    },
    [MessageTypes.ENABLE_HIGHLIGHT]() {
      handleEnableHighlight(message, sender, sendResponse);
    },
    [MessageTypes.DISABLE_HIGHLIGHT]() {
      handleDisableHighlight(message, sender, sendResponse);
    },
    [MessageTypes.DELETE_WORKSPACE]() {
      handleDeleteWorkspace(message, sender, sendResponse);
    },
    [MessageTypes.RELOAD_EXTENSION_APP]() {
      sendResponse(generateResponse({}));
      /**
       * NOTE: contentScript    backgroundScript refresh   ,
       *       backgroundScript  refresh  , refresh     tab  
       *       local storage  .
       * */
      saveImmediatelyOpenWidget(sender.tab);
      chrome.runtime.reload();
    },
    [MessageTypes.OPEN_SIGN_IN_PAGE]() {
      openSignInTab();
    },
    [MessageTypes.CHECK_GUIDE]() {
      handleCheckGuideVisibility(message, sender, sendResponse);
    },
    [MessageTypes.SET_GUIDE_SHOWED]() {
      handleSetGuideShowed(message, sender, sendResponse);
    },
  };

  const asyncAction = asyncActions && asyncActions[message.type];
  if (_.isFunction(asyncAction)) {
    asyncAction();
    return true;
  }

  const syncActions = {
    [MessageTypes.SET_WIDGET_OPENED_TAB_ID]() {
      handleSetWidgetOpenedPageId(message, sender, sendResponse);
    },
    [MessageTypes.RESET_WIDGET_OPENED_TAB_ID]() {
      handleResetWidgetOpenedPageId(message, sender, sendResponse);
    },
  };

  const syncAction = syncActions[message.type];
  if (_.isFunction(syncAction)) {
    syncAction();
  }
  return false;
}

export async function errorHandling(fn, sendResponse) {
  try {
    await fn(); // fn  async function  ,   
  } catch (e) {
    Sentry.captureException(e);
    sendResponse(
      generateResponse({
        ok: false,
        error: e,
        data: {},
      }),
    );
  }
}
