import generateResponse from 'common/generateResponse';
import { setGuideViewed } from 'background/stroage/guide';
import { errorHandling } from '../index';

export default async function(message, sender, sendResponse) {
  errorHandling(async function handleSetGuideShowed() {
    // available guidName: [LIBRARY_GUIDE || CREATE_FOLDER_GUIDE || OPEN_LIBRARY_GUIDE]
    //  === ['library_guide', 'create_folder_guide' || 'open_library_guide'];
    const {
      data: { guideName },
    } = message;
    setGuideViewed(guideName);
    sendResponse(generateResponse());
  }, sendResponse);
}
