import _ from 'lodash';

import { getWorkspaces } from 'background/stroage/workspace';
import { getUserId } from 'background/stroage/user';
import { getRecentFolderId } from 'background/stroage/folder';
import getFolders from './getFolders';

function getInitialStates({ workspaceDelta, libraryDelta }) {
  const currentWorkspace = workspaceDelta.created.workspace[0];
  const currentWorkspaceId = currentWorkspace.id;
  const myId = getUserId();
  const [me] = _.filter(currentWorkspace.members, ({ id }) => id === myId);

  const workspaces = _.map(getWorkspaces(), ws => {
    if (ws.id === currentWorkspaceId) {
      return currentWorkspace;
    }
    return ws;
  });

  const folders = getFolders(libraryDelta);

  let recentFolderId = getRecentFolderId();
  if (
    recentFolderId !== '' &&
    !_.has(libraryDelta.created.folders, recentFolderId)
  ) {
    //       library Root(:'')  .
    recentFolderId = '';
  }

  return {
    widget: {
      workspaces,
      folders,
      recentFolderId,
      currentWorkspaceId,
      user: me,
      myRole: me.role || 0,
    },
  };
}

export default getInitialStates;
