import _ from 'lodash';

export function queryTabs(condition, callback) {
  chrome.tabs.query(condition, callback);
}

export function getTabById(tabId, callback) {
  chrome.tabs.get(tabId, callback);
}

export function createTab({ active = true, url }) {
  if (!_.isString(url)) return;
  chrome.tabs.create({
    active,
    url,
  });
}

export function getCurrentTab(callback) {
  queryTabs({ active: true, currentWindow: true }, tabs => {
    callback(tabs[0]);
  });
}
