import generateResponse from 'common/generateResponse';
import putHighlights from 'background/api/rest/highlights/putHighlights';
import { errorHandling } from '../index';

export default async function(message, sender, sendResponse) {
  errorHandling(async () => {
    const {
      data: { highlightId, color },
    } = message;
    await putHighlights({
      id: highlightId,
      color,
    });
    sendResponse(generateResponse());
  }, sendResponse);
}
