import _ from 'lodash';
import generateResponse from 'common/generateResponse';
import { getCardId } from 'background/stroage/card';
import postHighlights from 'background/api/rest/highlights/postHighlights';
import { setWidgetOpenedTabId } from 'background/stroage/tabs';
import { errorHandling } from '../index';

async function createHighlightAndSendResponse(cardId, highlight, sendResponse) {
  const result = await postHighlights({
    targetId: cardId,
    highlight,
  });
  if (result.ok) {
    const createdHighlight = _.get(result, 'body.results[0]');
    sendResponse(
      generateResponse({
        data: {
          highlight: createdHighlight,
        },
      }),
    );
  }
}

export default async function(message, sender, sendResponse, callbacks = {}) {
  errorHandling(async () => {
    const tabId = sender.tab.id;
    const {
      data: { highlightElement },
    } = message;
    const targetCardId = getCardId(tabId);
    if (targetCardId) {
      await createHighlightAndSendResponse(
        targetCardId,
        highlightElement,
        sendResponse,
      );
    } else {
      if (_.isFunction(callbacks.setOnCreateCard)) {
        callbacks.setOnCreateCard(async createdCard => {
          await createHighlightAndSendResponse(
            createdCard.id,
            highlightElement,
            sendResponse,
          );
        });
      }
    }
    setWidgetOpenedTabId(tabId);
  }, sendResponse);
}
