import generateRequest from 'common/generateRequest';
import generateResponse from 'common/generateResponse';
import MessageTypes from 'common/MessageTypes';
import deleteComments from 'background/api/rest/comment/deleteComments';
import AdeleClient from 'background/core/AdeleClient';
import { deleteCardId, getCardId } from 'background/stroage/card';
import {
  deleteComment as deleteLocalComment,
  getComment,
} from 'background/stroage/comment';
import {
  getCurrentWorkspaceId,
  removeWorkspace,
  switchFirstWorkspace,
} from 'background/stroage/workspace';
import getInitialStates from '../../helpers/getInitialStates';
import { errorHandling } from '../index';

export default async function(message, sender, sendResponse) {
  errorHandling(async () => {
    const tabId = sender.tab.id;
    removeWorkspace(getCurrentWorkspaceId());
    AdeleClient.deleteLibraryCard(getCardId(tabId));
    deleteCardId(tabId);
    const localComment = getComment(tabId);
    if (localComment) {
      await deleteComments(localComment.id);
      deleteLocalComment(tabId);
    }

    await switchFirstWorkspace();
    const workspaceDelta = AdeleClient.getWorkspaceDelta();
    const libraryDelta = AdeleClient.getLibraryDelta();
    const initialStates = getInitialStates({
      workspaceDelta,
      libraryDelta,
    });
    generateResponse(generateResponse());

    chrome.tabs.sendMessage(
      tabId,
      generateRequest({
        type: MessageTypes.UPDATE_INITIAL_STATES,
        data: {
          states: initialStates || {},
        },
      }),
    );
  }, sendResponse);
}
