import { TypeNumbers } from '@additor/elements';
import generateResponse from 'common/generateResponse';
import LibraryDelta from 'background/core/delta/LibraryDelta';
import AdeleClient from 'background/core/AdeleClient';
import { errorHandling } from '../index';

export default async function(message, sender, sendResponse) {
  errorHandling(async () => {
    const { cardId } = message.data;
    const updatedTitle = message.data.title;
    const updatedTags = message.data.tags;
    const updatedThumbnail = message.data.thumbnail;

    const updatedData = {};

    if (updatedTitle) {
      updatedData.name = updatedTitle;
    }
    if (updatedTags) {
      updatedData.tags = updatedTags;
    }
    if (updatedThumbnail) {
      updatedData.images = {
        thumb: updatedThumbnail,
      };
    }

    const updateDelta = new LibraryDelta().update(
      {
        type: TypeNumbers.CARD,
        id: cardId,
      },
      {
        type: TypeNumbers.CARD,
        id: cardId,
        ...updatedData,
      },
      { a: 'update' },
    );

    const result = await AdeleClient.mutateDelta(updateDelta);

    if (!result) {
      throw Error('handleUpdateAdditCard');
    }
    if (result && result.error) {
      throw Error(result.error);
    }
    sendResponse(
      generateResponse({
        data: { ...result.data, updatedTitle, updatedThumbnail, updatedTags },
      }),
    );
  }, sendResponse);
}
