import { fetchGet } from 'background/api/rest/v2';

export default async function getAccess(accessToken) {
  let res = null;
  try {
    res = await fetchGet(
      '/access',
      {
        headers: {
          'X-Additor-Access': accessToken,
        },
      },
      false,
    );
  } catch (e) {
    console.error(e);
    // 401  Sentry  ( catch) -      
  }
  return res;
}
