import getApolloClient from '../ApolloClient';
import { fetchWorkspaces as queryFetchWorkspaces } from './query.graphql';

export default async function fetchWorkspaces(workspaceEids) {
  const result = await getApolloClient().query({
    query: queryFetchWorkspaces,
    context: {
      headers: {},
    },
    variables: {
      eids: workspaceEids,
    },
    fetchPolicy: 'no-cache',
  });
  return result.data.fetchBulk;
}
