import getHostName from 'common/getHostName';

let _workspaceAPIHost;

function generateAPIHost(subDomain) {
  return `https://${subDomain}.${getHostName()}`;
}

export async function initializeWorkspaceAPIHost(workspaceEid) {
  _workspaceAPIHost = generateAPIHost(workspaceEid);
}

export function getWorkspaceAPIHost() {
  return _workspaceAPIHost;
}

export function getAppAPIHost() {
  return `https://app.${getHostName()}`;
}
