import _ from 'lodash';
import MessageTypes from 'common/MessageTypes';
import generateRequest from 'common/generateRequest';
import getHostName from 'common/getHostName';
import { getWorkspaces } from 'background/stroage/workspace';
import { deleteComment } from '../stroage/comment';
import { getWidgetOpenedTabId, setWidgetOpenedTabId } from '../stroage/tabs';

function openExtensionSignInPopup(tabWidth) {
  chrome.tabs.query({}, tabs => {
    const signInPopup = _.find(tabs, ({ url }) => {
      return url === `https://${getHostName()}/chrome-extension-sign-in`;
    });
    if (signInPopup) {
      chrome.tabs.remove(signInPopup.id);
    }
    chrome.windows.create({
      url: `https://${getHostName()}/chrome-extension-sign-in`,
      type: 'popup',
      focused: true,
      width: 335,
      height: 560,
      left: tabWidth - 345,
      setSelfAsOpener: true,
    });
  });
}

function pollingOpenWidget(tabId, timeout = 1000, count = 0) {
  if (count > 10) {
    return;
  }
  setTimeout(() => {
    chrome.tabs.sendMessage(
      tabId,
      generateRequest({
        type: MessageTypes.OPEN_WIDGET,
      }),
      response => {
        if (!response || !response.ok) {
          pollingOpenWidget(tabId, timeout, count + 1);
        }
      },
    );
  }, timeout);
}

function isAvailableUrl(url) {
  return (
    !_.includes(url, 'alphamon.xyz') &&
    !_.includes(url, 'betamon.xyz') &&
    !_.includes(url, 'additor.io') &&
    !_.includes(url, 'additor.co')
  );
}

export default function onClickBrowserActionIcon(tab) {
  if (!isAvailableUrl(tab.url)) {
    return;
  }
  if (_.isEmpty(getWorkspaces())) {
    openExtensionSignInPopup(tab.width);
  } else {
    const tabId = tab.id;
    const widgetOpenedTabId = getWidgetOpenedTabId();
    if (widgetOpenedTabId && widgetOpenedTabId !== tabId) {
      deleteComment(tabId);
      chrome.tabs.sendMessage(
        widgetOpenedTabId,
        generateRequest({
          type: MessageTypes.RESET_WIDGET,
        }),
        _.noop,
      );
    }
    chrome.tabs.sendMessage(
      tabId,
      generateRequest({
        type: MessageTypes.OPEN_WIDGET,
      }),
      response => {
        setWidgetOpenedTabId(tabId);
        if (!response || !response.ok) {
          chrome.tabs.reload(tabId, null, () => {
            pollingOpenWidget(tabId);
          });
        }
      },
    );
  }
}
