import {
  STORAGE_KEYS,
  saveMap as saveToStorage,
  load as loadFromStorage,
} from 'background/chrome/storage';
import { getCurrentWorkspaceId } from './workspace';

let _recentFolder;

export async function loadRecentFolder(workspaceId) {
  const recentFolderIdsByWorkspace = await loadFromStorage(
    STORAGE_KEYS.RECENT_SELECTED_FOLDER,
  );
  setRecentFolderId(
    (recentFolderIdsByWorkspace && recentFolderIdsByWorkspace[workspaceId]) ||
      '',
  );
}

export function setRecentFolderId(folderId) {
  _recentFolder = folderId;
  saveToStorage(STORAGE_KEYS.RECENT_SELECTED_FOLDER, {
    [getCurrentWorkspaceId()]: _recentFolder,
  });
}

export function getRecentFolderId() {
  return _recentFolder;
}
