import * as Elements from '@additor/elements';
import { Registration, LibraryDelta } from '@additor/delta';

LibraryDelta.register(
  new Registration('cards', Elements.TypeNumbers.CARD, Elements.Card),
);
LibraryDelta.register(
  new Registration('folders', Elements.TypeNumbers.FOLDER, Elements.Folder),
);
LibraryDelta.register(new Registration('layouts', -1));

export default LibraryDelta;
