import ApolloClient from 'apollo-boost';
import { getAppAPIHost } from 'background/api/APIHost';

let _apolloClient;

export default function getApolloClient() {
  if (_apolloClient) {
    return _apolloClient;
  }
  const apiHost = getAppAPIHost();
  if (apiHost) {
    const apolloClient = new ApolloClient({
      uri: `${apiHost}/api/v2/_graphql`,
    });
    _apolloClient = apolloClient;
    return apolloClient;
  }
  return null;
}
