import _ from 'lodash';
import * as Sentry from '@sentry/browser';
import { Authority } from '@additor/elements';
import MessageTypes from 'common/MessageTypes';
import generateRequest from 'common/generateRequest';
import { COOKIE_NAMES } from 'background/utils/cookieManager';
import {
  getCookie,
  removeWorkspaceEidFromCookie,
} from 'background/utils/cookieManager';
import queryFetchWorkspaces from '../api/graphQL/fetchWorkspaces';
import getAccess from '../api/rest/access/getAccess';
import AdeleClient from '../core/AdeleClient';
import { loadRecentFolder } from './folder';
import { getWidgetOpenedTabId, setWidgetOpenedTabId } from './tabs';
import { getUserId, setUserId } from './user';
import { getCardId, getTabIdsWithCard } from './card';
import { initializeWorkspaceAPIHost } from '../api/APIHost';
import { getAccessToken } from '../token/accessToken';
import {
  load as loadFromStorage,
  save as saveToStorage,
  STORAGE_KEYS,
} from '../chrome/storage';
import getInitialStates from '../eventHandler/helpers/getInitialStates';
import sendMessageToDisableUserAction from '../sendMessage/sendMessageToDisableUserAction';

let _workspaces = [];
let _currentWorkspaceId = null;

export async function loadCurrentWorkspaceId() {
  const workspaceId = await loadFromStorage(STORAGE_KEYS.CURRENT_WORKSPACE_ID);
  if (workspaceId) {
    setCurrentWorkspaceId(workspaceId);
  }
}

export async function saveCurrentWorkspaceId(workspaceId) {
  return saveToStorage(STORAGE_KEYS.CURRENT_WORKSPACE_ID, workspaceId);
}

const isWorkspaceUser = (members, userId) =>
  _.filter(members, ({ id }) => id === userId).length === 0;

const isValidUser = (mutateDelta, snapshotDelta, userId) => {
  const updatedWorkspace = _.get(mutateDelta, 'updated.after.workspace.0');
  if (updatedWorkspace) {
    const updatedWorkspaceMember = _.get(
      snapshotDelta,
      'created.workspace.0.members',
    );
    if (
      updatedWorkspaceMember &&
      isWorkspaceUser(updatedWorkspaceMember, userId)
    ) {
      return false;
    }
  }
  return true;
};

function isGuest(wsd, myId) {
  const [me] = _.filter(
    wsd.created.workspace[0].members,
    ({ id }) => id === myId,
  );
  return me.role === Authority.EntireRoles['GUEST'];
}

export async function checkInToWorkspace(accessToken) {
  const res = await getAccess(accessToken);

  if (!res.ok) {
    // console.warn(' switch workspace -    ');
    sendMessageToDisableUserAction(getWidgetOpenedTabId());
    return null;
  }

  const { body } = res;
  const workspaceId = _.get(body, 'workspace.id');
  const user = _.get(body, 'workspaceUser');
  const userId = _.get(body, 'workspaceUser.id');
  const clientAgentId = _.get(body, 'clientAgent.id');
  if (workspaceId && userId && clientAgentId) {
    await saveCurrentWorkspaceId(workspaceId);
    await loadRecentFolder(workspaceId);
    setUserId(userId);

    Sentry.configureScope(scope => {
      scope.setUser({
        id: user?.id,
        username: user?.name,
        email: user?.email,
      });
    });

    const wsd = await AdeleClient.setWorkspaceTune({
      accessToken,
      clientAgentId,
      workspaceId,
      userId,
      onMutatingSuccess: async mutateDelta => {
        const workspaceDelta = AdeleClient.getWorkspaceDelta();
        if (!isValidUser(mutateDelta, workspaceDelta, userId)) {
          // console.warn(' ');
          const tabIds = getTabIdsWithCard();
          if (tabIds.length > 0) {
            _.forEach(tabIds, tabId => sendMessageToDisableUserAction(tabId));
          }
          return;
        }

        const libraryDelta = AdeleClient.getLibraryDelta();
        const initialStates = getInitialStates({
          workspaceDelta,
          libraryDelta,
        });

        const tabIds = getTabIdsWithCard();
        _.forEach(tabIds, tabId => {
          chrome.tabs.sendMessage(
            tabId,
            generateRequest({
              type: MessageTypes.UPDATE_INITIAL_STATES,
              data: {
                states: initialStates || {},
              },
            }),
          );
        });
      },
      onQueryingSuccess: _.noop,
      onDestroyed: () => {
        // console.warn(' ');
        const tabIds = getTabIdsWithCard();
        if (tabIds.length > 0) {
          _.forEach(tabIds, tabId => sendMessageToDisableUserAction(tabId));
        }
      },
    });

    if (isGuest(AdeleClient.getWorkspaceDelta(), getUserId())) {
      return wsd;
    }

    await AdeleClient.subscribeLibraryTune({
      onMutatingSuccess: deltaShell => {
        const widgetOpenedTabId = getWidgetOpenedTabId();
        if (!widgetOpenedTabId) return;

        //    TODO:     ,     
        const isCardDeleted = _.has(
          deltaShell,
          `d.deleted.cards.${getCardId(widgetOpenedTabId)}`,
        );
        if (isCardDeleted) {
          chrome.tabs.sendMessage(
            widgetOpenedTabId,
            generateRequest({
              type: MessageTypes.RESET_WIDGET,
            }),
            _.noop,
          );
          setWidgetOpenedTabId(null);
          return;
        }

        //     //   - updateInitialStates  
        const isFoldersUpdated =
          _.has(deltaShell.d.deleted, 'folders') ||
          _.has(deltaShell.d.updated, 'after.folders') ||
          _.has(deltaShell.d.created, 'folders');
        if (isFoldersUpdated) {
          const workspaceDelta = AdeleClient.getWorkspaceDelta();
          const libraryDelta = AdeleClient.getLibraryDelta();
          const initialStates = getInitialStates({
            workspaceDelta,
            libraryDelta,
          });

          const tabIds = getTabIdsWithCard();
          _.forEach(tabIds, tabId => {
            chrome.tabs.sendMessage(
              tabId,
              generateRequest({
                type: MessageTypes.UPDATE_INITIAL_STATES,
                data: {
                  states: initialStates || {},
                },
              }),
            );
          });
        }
      },
      onMutatingFail: error => {
        Sentry.captureException(error);
        const widgetOpenedTabId = getWidgetOpenedTabId();
        if (widgetOpenedTabId) {
          chrome.tabs.sendMessage(
            widgetOpenedTabId,
            generateRequest({
              type: MessageTypes.SHOW_ERROR_VIEW,
            }),
          );
        }
      },
    });

    return wsd;
  }
  return null;
}

export async function switchWorkspace(workspaceId) {
  setCurrentWorkspaceId(workspaceId);
  const currentWorkspace =
    _.find(_workspaces, ({ id }) => workspaceId === id) || _workspaces[0];
  if (!currentWorkspace) return null;
  await initializeWorkspaceAPIHost(currentWorkspace.eid);
  const accessToken = await getAccessToken(true); // Workspace       .
  if (!accessToken) return null;
  return await checkInToWorkspace(accessToken);
}

export async function switchFirstWorkspace() {
  if (_workspaces.length === 0) {
    return undefined;
  }
  const firstWorkspaceId = _workspaces[0].id;
  return await switchWorkspace(firstWorkspaceId);
}

export async function fetchWorkspaces(workspaceEids) {
  if (!_.isArray(workspaceEids)) {
    const cookie = await getCookie(COOKIE_NAMES.WORKSPACE_EIDS);
    workspaceEids = JSON.parse(decodeURIComponent(cookie));
  }
  if (_.isEmpty(workspaceEids)) {
    return [];
  }
  const resultWorkspaces = await queryFetchWorkspaces(workspaceEids);
  return _.map(workspaceEids || [], eid =>
    _.find(
      resultWorkspaces,
      eachResultWorkspace => eachResultWorkspace.eid === eid,
    ),
  );
}

export async function fetchAndSetWorkspaces(workspaceEids) {
  const workspaces = await fetchWorkspaces(workspaceEids);
  setWorkspaces(workspaces);
  return workspaces;
}

export function setWorkspaces(workspaces) {
  _workspaces = workspaces;
}

export function getWorkspaces() {
  return _.compact(_workspaces);
}

export function setCurrentWorkspaceId(workspaceId) {
  _currentWorkspaceId = workspaceId;
}

export function getCurrentWorkspaceId() {
  return _currentWorkspaceId;
}

export function getCurrentWorkspaceEid() {
  const currentWorkspace = _.filter(
    getWorkspaces(),
    ({ id }) => id === getCurrentWorkspaceId(),
  );
  if (currentWorkspace.length > 0) {
    return currentWorkspace[0].eid;
  } else {
    return '';
  }
}

export function removeWorkspace(workspaceId) {
  const workspaceEid = _.get(
    _.find(getWorkspaces(), { id: workspaceId }),
    'eid',
  );
  if (!workspaceEid) {
    return;
  }
  removeWorkspaceEidFromCookie(workspaceEid);
  const newWorkspaces = _.compact(
    _.filter(getWorkspaces(), ({ id }) => id !== workspaceId),
  );
  setWorkspaces(newWorkspaces);
  if (newWorkspaces.length > 0) {
    if (!_.includes(newWorkspaces, { id: getCurrentWorkspaceId() })) {
      setCurrentWorkspaceId(newWorkspaces[0].id);
    }
  } else {
    setCurrentWorkspaceId('');
  }
}
