import { Utils } from '@additor/adele-client';
import { WorkspaceDelta } from '@additor/delta';
import * as Elements from '@additor/elements';

export function generateCreateChannelDelta({ channelName, channelDesc = '' }) {
  const localId = Utils.generateLocalElementId();
  const newLayout = {
    type: -1,
    _type: Elements.TypeNumbers.CHANNEL,
    id: localId,
  };
  const channelToCreate = new Elements.Channel({
    id: localId,
    name: channelName,
    desc: channelDesc,
  });
  return {
    localId,
    workspaceDelta: new WorkspaceDelta()
      .create(channelToCreate, { a: 'create' })
      .create(newLayout),
  };
}

export function generateCreatePageDelta({
  pageName,
  pageDesc = '',
  channelId,
}) {
  const localId = Utils.generateLocalElementId();
  const newPage = new Elements.Page()
    .setId(localId)
    .setName(pageName)
    .setDescription(pageDesc);
  const newLayout = {
    type: -1,
    _type: newPage.type,
    id: localId,
    parent: channelId,
  };

  return {
    localId,
    workspaceDelta: new WorkspaceDelta()
      .create(newPage, { a: 'create' })
      .create(newLayout),
  };
}

export function handleError(error) {
  if (error.status === 410) {
    console.log('deleted workspace');
  } else {
    console.error(error.status);
    throw Error(error);
  }
}
