import MessageTypes from 'common/MessageTypes';
import getLandingHost from '../utils/getLandingHost';

import { createTab, getCurrentTab } from './tab';
import { loadHighlightable, saveHighlightable } from '../stroage/settings';
import sendMessageToShowEnableHighlightAlert from '../sendMessage/sendMessageToShowEnableHighlightAlert';
import sendMessageToShowDisableHighlightAlert from '../sendMessage/sendMessageToShowDisableHighlightAlert';
import onClickBrowserActionIcon from '../eventHandler/onClickBrowserActionIcon';

export function createContextMenu(contextMenu) {
  return chrome.contextMenus.create(contextMenu);
}

export function createBrowserActionContextMenu() {
  createContextMenu({
    type: 'normal',
    title: 'Open your Additor home',
    contexts: ['browser_action'],
    onclick: () => {
      createTab({
        url: getLandingHost(),
      });
    },
  });

  createContextMenu({
    type: 'normal',
    title: 'Customize shortcuts',
    contexts: ['browser_action'],
    onclick: () => {
      createTab({
        url: 'chrome://extensions/shortcuts',
      });
    },
  });

  createContextMenu({
    type: 'normal',
    title: 'Extension Settings',
    contexts: ['browser_action'],
    onclick: () => {
      getCurrentTab(tab => {
        chrome.tabs.sendMessage(tab.id, {
          type: MessageTypes.SHOW_SETTINGS,
        });
      });
    },
  });
}

export function createPageContextMenu() {
  const additorContextMenuId = createContextMenu({
    type: 'normal',
    title: 'Additor',
  });

  createContextMenu({
    parentId: additorContextMenuId,
    type: 'normal',
    title: 'Save this page',
    contexts: ['all'],
    onclick: (info, tab) => {
      onClickBrowserActionIcon(tab);
    },
  });

  createContextMenu({
    parentId: additorContextMenuId,
    type: 'normal',
    title: 'Turn on/off highlighter',
    contexts: ['all'],
    onclick: async (info, tab) => {
      const highlightable = await loadHighlightable();
      const nextHighlightable = !highlightable;
      await saveHighlightable(nextHighlightable);
      if (nextHighlightable) {
        sendMessageToShowEnableHighlightAlert(tab);
      } else {
        sendMessageToShowDisableHighlightAlert(tab);
      }
    },
  });

  createContextMenu({
    parentId: additorContextMenuId,
    type: 'normal',
    title: 'Extension Settings',
    contexts: ['all'],
    onclick: () => {
      getCurrentTab(tab => {
        chrome.tabs.sendMessage(tab.id, {
          type: MessageTypes.SHOW_SETTINGS,
        });
      });
    },
  });
}
