import _ from 'lodash';

export default function getFolders(libraryDelta) {
  const folderIds = _.keys(libraryDelta.created.folders);

  const getSortedFolders = _.flow(
    (folderIds, layouts) => {
      return _.map(folderIds, id => {
        return _.get(layouts, `[${id}].0`);
      });
    },
    folderLayouts => {
      if (folderLayouts.length === 0) return [];

      const sortedLayout = [];
      let currentLayout = _.find(folderLayouts, { prev: null });
      do {
        sortedLayout.push(currentLayout);
        if (!currentLayout.next) {
          break;
        }
        currentLayout = _.find(folderLayouts, { id: currentLayout.next });
      } while (sortedLayout.length <= folderLayouts.length);

      return _.map(sortedLayout, ({ id }) => id);
    },
    ids => folders =>
      _.map(ids, id => {
        const folder = folders[id][0];
        return {
          id: folder.id,
          name: folder.name,
        };
      }),
  );
  return getSortedFolders(folderIds, libraryDelta.created.layouts)(
    libraryDelta.created.folders,
  );
}
