import _ from 'lodash';
import generateResponse from 'common/generateResponse';
import AdeleClient from 'background/core/AdeleClient';
import { getAccessToken } from 'background/token/accessToken';
import { initializeWorkspaceAPIHost } from 'background/api/APIHost';
import getAccess from 'background/api/rest/access/getAccess';
import sendMessageToDisableUserAction from 'background/sendMessage/sendMessageToDisableUserAction';
import { deleteCardId, getTabIdsWithCard } from 'background/stroage/card';
import {
  deleteComment as deleteLocalComment,
  getComment,
} from 'background/stroage/comment';
import {
  getCurrentWorkspaceId,
  getCurrentWorkspaceEid,
  switchFirstWorkspace,
  removeWorkspace,
  switchWorkspace,
  checkInToWorkspace,
} from 'background/stroage/workspace';
import getInitialStates from '../helpers/getInitialStates';
import { errorHandling } from './index';

export default async function(message, sender, sendResponse) {
  errorHandling(async () => {
    const currentWorkspace = getCurrentWorkspaceEid();
    await initializeWorkspaceAPIHost(currentWorkspace);
    const accessToken = await getAccessToken();

    if (!accessToken) {
      sendResponse(
        generateResponse({
          ok: false,
          error: 'Token Expired',
        }),
      );
      return;
    }
    const res = await getAccess(accessToken);
    if (!res.ok) {
      //  accessToken   workspace     
      //  workspace  cookie  ,   switch
      const tabIds = getTabIdsWithCard();
      if (tabIds.length > 0) {
        _.forEach(tabIds, tabId => sendMessageToDisableUserAction(tabId));
      }

      const tabId = sender.tab.id;
      removeWorkspace(getCurrentWorkspaceId());
      deleteCardId(tabId);
      const localComment = getComment(tabId);
      if (localComment) {
        deleteLocalComment(tabId);
      }
      await switchFirstWorkspace();
    }

    deleteLocalComment(sender.tab.id);
    deleteCardId(sender.tab.id);

    if (!AdeleClient.workspaceTune) {
      await checkInToWorkspace(accessToken);
    }

    let workspaceDelta = AdeleClient.getWorkspaceDelta();
    if (!workspaceDelta) {
      chrome.runtime.reload();
      return;
    }

    let libraryDelta = AdeleClient.getLibraryDelta();

    if (!libraryDelta) {
      chrome.runtime.reload();
      return;
    }

    // AdeleClient   workspaceDelta currentWorkspaceId
    if (workspaceDelta.created.workspace[0].id !== getCurrentWorkspaceId()) {
      await switchWorkspace(getCurrentWorkspaceId());
      workspaceDelta = AdeleClient.getWorkspaceDelta();
    }

    const initialStates = getInitialStates({
      workspaceDelta,
      libraryDelta,
    });

    sendResponse(
      generateResponse({
        data: { initialStates },
      }),
    );
  }, sendResponse);
}
