import {
  load as loadFromStorage,
  save as saveToStorage,
  STORAGE_KEYS,
} from '../chrome/storage';

let guideStatusMap = {};

export async function saveGuideStatus() {
  return saveToStorage(STORAGE_KEYS.GUIDE, guideStatusMap);
}

export async function getGuideStatus(guideName) {
  const guidesStatus = await loadFromStorage(STORAGE_KEYS.GUIDE);
  if (guidesStatus) {
    return guidesStatus[guideName];
  } else {
    guideStatusMap[guideName] = false;
    saveGuideStatus();
    return false;
  }
}

export function setGuideViewed(guideName) {
  guideStatusMap[guideName] = true;
  saveGuideStatus();
}
