import {
  load as loadFromStorage,
  save as saveToStorage,
  STORAGE_KEYS,
} from '../chrome/storage';

export async function saveHighlightable(highlightable) {
  return saveToStorage(STORAGE_KEYS.HIGHLIGHTABLE, highlightable);
}

export async function loadHighlightable() {
  const highlightable = await loadFromStorage(STORAGE_KEYS.HIGHLIGHTABLE);
  if (highlightable === undefined) return true;
  return highlightable;
}
