import { fetchRequest } from './fetchRequest';
const { version } = require('../../../../package.json');

export async function fetchGet(url, options = {}, useToken = true) {
  const headers = {
    ...options.headers,
    'X-Additor-Platform': `chrome_extension/${version}`,
  };

  return await fetchRequest({
    url,
    options: {
      ...options,
      method: 'GET',
      headers,
    },
    version: 'v3',
    useToken,
  });
}

