import _ from 'lodash';

let _cardsByTabId = {};

export function setCardId({ tabId, cardId }) {
  _cardsByTabId[tabId] = cardId;
}

export function getCardId(tabId) {
  return _cardsByTabId[tabId];
}

export function deleteCardId(tabId) {
  delete _cardsByTabId[tabId];
}

export function getTabIdsWithCard() {
  return _.map(_.keys(_cardsByTabId), stringTabId => _.toNumber(stringTabId));
}
