import getLandingHost from 'background/utils/getLandingHost';

export const COOKIE_NAMES = {
  WORKSPACE_EIDS: 'additor_weids',
  LATEST_WORKSPACE: 'latest_ws',
  EXCHANGE: 'exchange',
  LIBRARY_GUIDE: 'library_guide',
  CREATE_FOLDER_GUIDE: 'create_folder_guide',
  OPEN_LIBRARY_GUIDE: 'open_library_guide',
};

export async function getCookie(cName) {
  return new Promise(resolve => {
    chrome.cookies.get(
      {
        url: getLandingHost(),
        name: cName,
      },
      cookie => {
        resolve(cookie?.value || null);
      },
    );
  });
}

export function setCookie(cName, cValue, cDay, cMin = 0) {
  return new Promise((resolve, reject) => {
    const expire = new Date();
    expire.setDate(expire.getDate() + cDay);
    expire.setTime(expire.getTime() + 1000 * 60 * cMin);
    //    escape(cValue) .
    const landingHost = getLandingHost();
    let domain = 'additor.io';
    if (landingHost === 'https://alphamon.xyz') {
      domain = 'alphamon.xyz';
    }
    if (landingHost === 'https://betamon.xyz') {
      domain = 'betamon.xyz';
    }
    chrome.cookies.set(
      {
        url: landingHost,
        name: cName,
        value: escape(cValue),
        path: '/',
        domain,
        expirationDate: expire.getTime() / 1000,
      },
      cookie => {
        if (cookie) {
          resolve(cookie);
        } else {
          reject();
        }
      },
    );
  });
}

export async function removeExchangeTokenFromCookie() {
  await setCookie(COOKIE_NAMES.WORKSPACE_EIDS, JSON.stringify([]), 30);
  await setCookie(COOKIE_NAMES.EXCHANGE, '', 30);
  await setCookie(COOKIE_NAMES.LATEST_WORKSPACE, '', 30);
}

export async function removeWorkspaceEidFromCookie(workspaceEid) {
  const cookie = await getCookie(COOKIE_NAMES.WORKSPACE_EIDS);
  const workspaceEids = JSON.parse(decodeURIComponent(cookie));
  const newWorkspaceEids = workspaceEids.filter(eid => eid !== workspaceEid);

  await setCookie(
    COOKIE_NAMES.WORKSPACE_EIDS,
    JSON.stringify(newWorkspaceEids),
    30,
  );
  await setCookie(COOKIE_NAMES.LATEST_WORKSPACE, '', 30);
}
