import MessageTypes from 'common/MessageTypes';
import generateRequest from 'common/generateRequest';
import { getTabIdsWithCard } from 'background/stroage/card';
import _ from 'lodash';

export default function(workspaces) {
  _.forEach(getTabIdsWithCard(), tabId =>
    chrome.tabs.sendMessage(
      tabId,
      generateRequest({
        type: MessageTypes.UPDATE_WORKSPACES,
        data: { workspaces },
      }),
      () => {},
    ),
  );
}
